.row {
  color: white;
  margin-left: 20px;
}

.row__posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
}

.row__posters::-webkit-scrollbar {
  display: none;
}

.row__poster {
  object-fit: contain; /* keeps ratio*/
  width: 100%; /* fits image to the screensize*/
  max-height: 100px;
  margin-right: 10px;
  transition: transform 350ms;
}

.row__poster:hover {
  transform: scale(1.08);
  /* opacity: 1; */
}

.row__posterLarge {
  max-height: 250px;
}

.row__posterLarge:hover {
  transform: scale(1.09);
  /* opacity: 1; */
}

.row__clip_container {
  display: flex;
  justify-content: center;
}
